import React, {ReactElement, useEffect, useState} from "react";
import {AppData} from "../../model/app-data";
import axios, {AxiosResponse} from "axios";

export interface AppDataStorageProps {
    children: ReactElement
}

export const AppDataStorage = (props: AppDataStorageProps) => {

    const [data, setData] = useState<AppData | undefined>();

    const init = async () => {
        const res: AxiosResponse<AppData> = await axios.get(`${process.env.PUBLIC_URL}/config.json`)
        if (res?.data) {
            console.log(res);
            setData(res.data);
        }
    }

    useEffect(() => {
        init()
    }, [])

    return !data ? null : <AppDataContext.Provider value={data}>
        {props.children}
    </AppDataContext.Provider>
}

export const AppDataContext = React.createContext<AppData>(null as any)