export class UrlBuilder {

    public static BASE_URL  = "";

    public static EXCURSIONS = "excursions";

    public static withEcursion(id: number):ExcursionBuilder{
        return new ExcursionBuilder(id);
    };
}

export class ExcursionBuilder {
    private excursionId: number;

    constructor(excursionId: number) {
        this.excursionId = excursionId;
    }

    public build(){
        const res = `${UrlBuilder.BASE_URL}/${UrlBuilder.EXCURSIONS}`;
        if (typeof this.excursionId === "number"){
            return `${res}/${this.excursionId}`;
        }
        return res;
    }
}