import {Map as YMap, Placemark, Polyline} from "react-yandex-maps";
import React, {useContext, useMemo} from "react";
import {makeStyles} from "tss-react/mui";
import {styles} from "./execution-page.styles";
import {
    Avatar,
    Fade,
    Grow,
    List,
    ListItem,
    ListItemAvatar,
    ListItemButton,
    ListItemText,
    Slide,
    Typography
} from "@mui/material";
import {AppDataContext} from "../../component/app-data/app-data.component";
import {AppData} from "../../model/app-data";
import {useParams} from "react-router-dom";
import {Place} from "../../model/place";
import {AppPanel} from "../../component/app-panel/app-panel.component";
import {AppPage} from "../../component/app-page/app-page.component";
import {Spacer} from "../../component/spacer/spacer.component";
import {Image} from "@mui/icons-material";


export interface ExcursionPageProps {

}

const useStyles = makeStyles<ExcursionPageProps>()(styles);

export const ExcursionPage = (props: ExcursionPageProps) => {

    const {classes} = useStyles(props);

    const data = useContext<AppData>(AppDataContext);

    let params = useParams();

    const excursion = data.excursions.find(it => `${it.id}` === params.id);


    const route = useMemo(() => excursion ? excursion.route.map(it => [it.lat, it.lon]) : [], [excursion]);

    const placesMap = useMemo(() => data.places ? data.places.reduce((prev, current) => {
        prev.set(current.id, current);
        return prev;
    }, new Map<number, Place>()) : new Map<number, Place>(), [data])

    const places: Place[] = useMemo(() => (excursion?.places ? excursion.places.map(it => placesMap.get(it)) : []).filter(it => it) as any, [data])

    if (!excursion) {
        return null;
    }


    return <AppPage className={classes.root}>

        <AppPanel className={classes.col}>
            <Typography variant={"h4"}>
                {excursion.caption}
            </Typography>
            <Spacer/>
            <Typography variant={"body1"}>
                {excursion.description}
            </Typography>
            <Spacer/>
            <List>
                {places.map((it, index) => <ListItem alignItems="flex-start" key={index}>
                    <ListItemButton>
                        <ListItemAvatar>
                            <Avatar variant={"square"} alt="Remy Sharp" src={it.mainPictureUrl}/>
                        </ListItemAvatar>

                        <ListItemText primary={it.caption}
                                      secondary={it.description}
                        />
                    </ListItemButton>
                </ListItem>)}
            </List>
        </AppPanel>
        <Spacer/>
        <AppPanel className={classes.col}>
            <YMap key={"sf"} className={classes.map}
                  options={{
                      suppressMapOpenBlock: true,
                      suppressObsoleteBrowserNotifier: true,
                  }}
                  defaultState={{
                      center: [54.978433, 73.379139],
                      zoom: 15.4,
                      restrictMapArea: true,
                      suppressMapOpenBlock: true,
                      suppressObsoleteBrowserNotifier: true,
                      behaviors: [],
                      controls: [],

                  }}>
                <Polyline geometry={route} options={{
                    balloonCloseButton: true,
                    strokeColor: '#ed4543',
                    strokeWidth: 8,
                    strokeOpacity: 0.5
                }}/>
                <Placemark geometry={[54.98236414119374, 73.38130654440984]}/>
                <Placemark geometry={[54.97440382276287, 73.37674678908404]}/>
            </YMap>
        </AppPanel>

    </AppPage>
}