import {AppBar, IconButton} from "@mui/material";
import Toolbar from '@mui/material/Toolbar';
import React, {useState} from "react";
import {ArrowBack as BackIcon, Home, QrCode} from "@mui/icons-material";
import {makeStyles} from "tss-react/mui";
import {styles} from "./main-frame.styles";
import {Route, Routes} from "react-router-dom";
import {MainPage} from "../../pages/main-page/main-page.component";
import {ExcursionPage} from "../../pages/excursion-page/excursion-page.component";
import {useNavigate} from "react-router-dom";
import {ExcursionListPage} from "../../pages/excursion-list-page/excursion-list-page.component";
import {UrlBuilder} from "../../utils/url.builder";
import {QrCodeDialog} from "./qrcode-dialog.component";


export interface MainFrameProps {

}

const useStyles = makeStyles<MainFrameProps>()(styles);

export const MainFrame = (props: MainFrameProps) => {

    const {classes} = useStyles(props);
    const navigate = useNavigate();
    const [qrCodeVisible, setQrCodeVisible] = useState(false);

    const goBack = () => {
        navigate(-1);
    }

    const goHome = () => {
        navigate(UrlBuilder.BASE_URL || "/");
    }

    const openQrCode = () => {
        setQrCodeVisible(true)
    }
    const onCloseQrCode = () => {
        setQrCodeVisible(false);
    }

    return <div className={classes.root}>
        <Routes>
            <Route element={<ExcursionPage/>} path={"/excursions/:id"}/>
            <Route element={<ExcursionListPage/>} path={"/excursions"}/>
            <Route element={<MainPage/>} path={"*"}/>
        </Routes>
        <AppBar className={classes.bar} position="fixed" color="primary" sx={{top: 'auto', bottom: 0}}>
            <Toolbar>
                <div className={classes.background}/>
                <div className={classes.tools}>
                    <div className={classes.toolsLeft}>
                        <IconButton onClick={goBack}>
                            <BackIcon/>
                        </IconButton>
                    </div>
                    <div className={classes.toolsCenter}>
                        <IconButton onClick={goHome}>
                            <Home/>
                        </IconButton>
                    </div>
                    <div className={classes.toolsRight}>
                        <IconButton>
                            <QrCode onClick={openQrCode}/>
                            {qrCodeVisible &&  <QrCodeDialog onClose={onCloseQrCode}/>}
                        </IconButton>
                    </div>
                </div>
            </Toolbar>
        </AppBar>
    </div>
}