import React from "react";
import {makeStyles} from "tss-react/mui";
import {styles} from "./app-panel.styles";
import  clx from "classnames";

export interface AppPanelProps {
    children?: React.ReactElement | React.ReactElement[] | never[],
    className?: string
}

const useStyles = makeStyles<AppPanelProps>()(styles)

export const  AppPanel = (props: AppPanelProps) => {

    const {classes} = useStyles(props);

    return <div className={clx(classes.root, props.className)}>
        {props.children || null}
    </div>
}