import {Excursion} from "../../model/excursion";
import {Card, CardActionArea, CardContent, CardMedia, Typography} from "@mui/material";
import {makeStyles} from "tss-react/mui";
import {styles} from "./excursion-list-card.styles";
import {useNavigate} from "react-router-dom";
import {UrlBuilder} from "../../utils/url.builder";

export interface ExcursionListCardProps {
    data: Excursion
}

const useStyles = makeStyles<ExcursionListCardProps>()(styles);

export const ExcursionListCard = (props: ExcursionListCardProps) => {

    const  {classes} = useStyles(props);

    const navigation  = useNavigate();

    const onCardClick = ()=>{
        navigation(UrlBuilder.withEcursion(props.data.id).build())
    }

    return <Card sx={{maxWidth: 345}} className={classes.root} onClick={onCardClick}>
        <CardActionArea>
            <CardMedia
                component="img"
                height="140"
                image={props.data.mainPictureUrl}
            />
            <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                    {props.data.caption}
                </Typography>
                <Typography variant="body2" color="text.secondary">
                    {props.data.description}
                </Typography>
            </CardContent>
        </CardActionArea>
    </Card>
}