import React from "react";
import {makeStyles} from "tss-react/mui";
import {MainFrameProps} from "../../frame/main-frame/main-frame.component";
import {styles} from "./navigation-button.styles";
import {Button, Grow} from "@mui/material";
import {useNavigate} from "react-router-dom";


export interface NavigationButtonProps {
    icon: React.ReactNode,
    title: string,
    linkUrl: string
}

const useStyles = makeStyles<MainFrameProps>()(styles);

export const NavigationButton = (props: NavigationButtonProps) => {

    const {classes} = useStyles(props);
    const navigation = useNavigate();

    const onClick = () => {
        navigation(props.linkUrl)
    }

    return <Grow in={true}>
        <Button className={classes.root} startIcon={props.icon} variant={"outlined"} onClick={onClick}>
        <div>{props.title}</div>
    </Button>
    </Grow>
}